import { Component } from '@angular/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import { FormBuilder, Validators } from '@angular/forms';
import { DatabaseProvider } from '../../../providers/database/database';
import { ImageProvider } from '../../../providers/image/image';
import { ResourcesProvider } from '../../../providers/resources/resources';

@Component({
  selector: 'page-edit-group',
  templateUrl: 'edit-group.html',
})
export class EditGroupPage {

  id: any;
  imageURL: string;

  formControl: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public toastCtrl: ToastController,
    public database: DatabaseProvider, public formBuilder: FormBuilder, public imgProvider: ImageProvider,
    public resources: ResourcesProvider) {
    this.id = this.navParams.get('id');
    let oldName = this.navParams.get('name');
    let oldCtg = this.navParams.get('category');
    let oldDesc = this.navParams.get('description');
    this.imageURL = this.navParams.get('image');

    this.formControl = this.formBuilder.group({
      name: [oldName, Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_NAME), Validators.required])],
      category: [oldCtg, Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_SUB)])],
      description: [oldDesc, Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_DESC)])]
    });
  }

  save() {
    if (this.formControl.valid) {
      this.database.updateGroup(
        this.id,
        this.formControl.get('name').value,
        this.formControl.get('category').value,
        this.formControl.get('description').value,
        this.imageURL
      );
      this.navCtrl.pop();
      this.doToast("Updates saved!");
    }
    else this.doToast("There is an issue with some of the values you have entered!");
  }

  uploadPicture(event: Event) {
    let file = event.target['files'].item(0);
    let self = this;

    if (!file) return;  // When the user doesn't provide a file

    if (file.type.split('/')[0] == 'image') {
      this.imgProvider.open();
      this.imgProvider.uploadGroupImage(file).then((snapshot: any) => {
        snapshot.ref.getDownloadURL().then(function (downloadURL) {
          self.imageURL = downloadURL;
        });
        this.imgProvider.close();
      });
    } else console.log("Not Image Type");
  }

  doToast(msg) {
    const toast = this.toastCtrl.create({
      message: msg,
      duration: 3000
    });
    toast.present();
  }

}



