import { Component } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { NavController, ToastController } from 'ionic-angular';
import { SignupPicturePage } from '../signup-picture/signup-picture';
import { ResourcesProvider } from '../../../providers/resources/resources';

@Component({
  selector: 'page-signup-details',
  templateUrl: 'signup-details.html',
})
export class SignupDetailsPage {

  formControl = this.formBuilder.group({
    name: ['', Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_NAME), Validators.pattern(/^[a-z ,.'-]+$/i), Validators.required])],
    intro: ['', Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_SUB)])],
    bio: ['', Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_DESC)])]
  });

  constructor(public navCtrl: NavController, public toastCtrl: ToastController,
    public formBuilder: FormBuilder, public resources: ResourcesProvider) { }

  nextPage() {
    if (this.formControl.valid) {
      this.navCtrl.push(SignupPicturePage, {
        name: this.formControl.get('name').value,
        intro: this.formControl.get('intro').value,
        bio: this.formControl.get('bio').value
      });
    } else this.detailsToast();
  }

  detailsToast() {
    const toast = this.toastCtrl.create({
      message: "There is an issue with some of the values you have entered!",
      duration: 3000
    });
    toast.present();
  }
}



