import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AzureAdProvider } from '../providers/azure-ad/azure-ad';

import { LoginPage } from '../pages/login/login';
import { HomePage } from '../pages/home/home';

import * as firebase from 'firebase';

const firebaseConfig = {
  apiKey: "AIzaSyA-KRXx4OqCmLW9_JAV7mpBqrbLJwV56GA",
  authDomain: "grouper-swen325-2018.firebaseapp.com",
  databaseURL: "https://grouper-swen325-2018.firebaseio.com",
  projectId: "grouper-swen325-2018",
  storageBucket: "grouper-swen325-2018.appspot.com",
  messagingSenderId: "1001940929452"
};

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage: any;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private azure: AzureAdProvider) {
    platform.ready().then(() => {
      if (this.azure.isAuthenticated()) this.rootPage = HomePage;
      else this.rootPage = LoginPage;
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });

    firebase.initializeApp(firebaseConfig);
  }
}



