import { Component } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { NavController, ToastController } from 'ionic-angular';
import { DatabaseProvider } from '../../../providers/database/database';

import { GroupsListPage } from '../groups-list/groups-list';
import { ImageProvider } from '../../../providers/image/image';
import { ResourcesProvider } from '../../../providers/resources/resources';

@Component({
  selector: 'page-create-group',
  templateUrl: 'create-group.html',
})
export class CreateGroupPage {
  imageURL: string;
  formControl: any;

  constructor(public navCtrl: NavController, public toastCtrl: ToastController,
    public database: DatabaseProvider, public formBuilder: FormBuilder,
    public imgProvider: ImageProvider, public resources: ResourcesProvider) {
    this.imageURL = 'assets/imgs/icon.png';

    this.formControl = this.formBuilder.group({
      name: ['', Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_NAME), Validators.required])],
      category: ['', Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_SUB)])],
      description: ['', Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_DESC)])]
    });
  }

  create() {
    if (this.formControl.valid) {
      this.database.createGroup(
        this.formControl.get('name').value,
        this.formControl.get('category').value,
        this.formControl.get('description').value,
        this.imageURL
      );
      this.navCtrl.setRoot(GroupsListPage);
      this.doToast(`Group "${this.formControl.get('name').value}" created!`);
    }
    else this.doToast("There is an issue with some of the values you have entered!");
  }

  uploadPicture(event: Event) {
    let file = event.target['files'].item(0);
    let self = this;

    if (!file) return;  // When the user doesn't provide a file

    if (file.type.split('/')[0] == 'image') {
      this.imgProvider.open();
      this.imgProvider.uploadGroupImage(file).then((snapshot: any) => {
        snapshot.ref.getDownloadURL().then(function (downloadURL) {
          self.imageURL = downloadURL;
        });
        this.imgProvider.close();
      });
    } else console.log("Not Image Type");
  }

  doToast(msg) {
    const toast = this.toastCtrl.create({
      message: msg,
      duration: 3000
    });
    toast.present();
  }

}



