import { Injectable } from '@angular/core';
import { Adal5Service } from 'adal-angular5';

@Injectable()
export class AzureAdProvider {
  private config = {                           
    tenant: 'myvuwac.onmicrosoft.com',                      
    clientId: 'd86cfb48-98a0-408e-80a5-3dd2e7e6aabb'    
  }

  constructor(private service: Adal5Service) {
  }

  // ========== User Information ========== \\

  public getUserID(): string {
    return this.service.userInfo.profile.oid;
  }

  public isAuthenticated(): boolean {
    return this.service.userInfo.authenticated;
  }

  // ========== Core Functionality ========== \\

  public initiateProvider() {
    this.service.init(this.config);
  }

  public handleCallback() {
    this.service.handleWindowCallback();
  }

  public login() {
    this.service.login();
  }

  public logout() {
    this.service.logOut();
  }

}



