import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { LoadingController, Loading } from 'ionic-angular';
import { AzureAdProvider } from '../azure-ad/azure-ad';
import * as firebase from 'firebase';

@Injectable()
export class ImageProvider {

  loading: Loading;

  constructor(public http: HttpClient, public loadingCtrl: LoadingController, private azure: AzureAdProvider) {
  }

  open() {
    this.loading = this.loadingCtrl.create({
      content: "Uploading your image..."
    });
    this.loading.present();
  }

  close() {
    this.loading.dismiss();
  }

  
  // ========== Image Storage  ========== \\

  uploadUserImage(file: File): Promise<any> {
    let image: string = `${this.azure.getUserID()}.${file.type.split('/')[1]}`,
      storageRef: any,
      parseUpload: any;

    return new Promise((resolve, reject) => {
      storageRef = firebase.storage().ref('users/' + image);
      parseUpload = storageRef.put(file);

      parseUpload.on('state_changed',
        () => { },
        (error) => reject(error),
        () => resolve(parseUpload.snapshot)
      );
    });
  }

  uploadGroupImage(file: File): Promise<any> {
    let image: string = `${new Date().getTime()}.${file.type.split('/')[1]}`,
      storageRef: any,
      parseUpload: any;

    return new Promise((resolve, reject) => {
      storageRef = firebase.storage().ref('groups/' + image);
      parseUpload = storageRef.put(file);

      parseUpload.on('state_changed',
        () => { },
        (error) => reject(error),
        () => resolve(parseUpload.snapshot)
      );
    });
  }

}



