import { Component } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import { DatabaseProvider } from '../../../providers/database/database';
import { ResourcesProvider } from '../../../providers/resources/resources';

@Component({
  selector: 'page-post',
  templateUrl: 'post.html',
})
export class PostPage {

  id: any;

  formControl: any;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public toastCtrl: ToastController, public formBuilder: FormBuilder,
    public database: DatabaseProvider, public resources: ResourcesProvider) {
    this.id = this.navParams.get('id');

    this.formControl = this.formBuilder.group({
      content: ["", Validators.compose([Validators.maxLength(this.resources.MAX_LENGTH_POST), Validators.required])]
    });
  }

  save() {
    if (this.formControl.valid) {
      this.database.createPost(
        this.id,
        this.formControl.get('content').value
      );
      this.navCtrl.pop();
      this.doToast("Post created!");
    }
    else this.doToast("Can't have an empty post!");
  }

  doToast(msg) {
    const toast = this.toastCtrl.create({
      message: msg,
      duration: 3000
    });
    toast.present();
  }

}



